<template>
	<view>
		<block v-for="(item, index) in list">
			<!-- 列表样式 -->
			<common-list :item="item" :index="index"></common-list>
			<divider></divider>
		</block>
	</view>
</template>

<script>
import commonList from '@/components/common/common-list.vue';
export default {
	components: {
		commonList
	},
	data() {
		return {
			list: [
				{
					username: '昵称1',
					userpic: '/static/default.jpg',
					newstime: '2020-05-24 上午 11：31',
					isFollow: false,
					title: '这是一个标题',
					titlepic: '/static/demo/banner2.jpg',
					support: {
						type: '',
						support_count: 0,
						unsupport_count: 0
					},
					comment_count: 4,
					share_num: 2
				},
				{
					username: '昵称1',
					userpic: '/static/default.jpg',
					newstime: '2020-05-24 上午 11：31',
					isFollow: false,
					title: '这是一个标题',
					titlepic: '/static/demo/banner2.jpg',
					support: {
						type: '',
						support_count: 0,
						unsupport_count: 0
					},
					comment_count: 4,
					share_num: 2
				},
				{
					username: '昵称1',
					userpic: '/static/default.jpg',
					newstime: '2020-05-24 上午 11：31',
					isFollow: false,
					title: '这是一个标题',
					titlepic: '/static/demo/banner2.jpg',
					support: {
						type: '',
						support_count: 0,
						unsupport_count: 0
					},
					comment_count: 4,
					share_num: 2
				}
			]
		};
	},
	onLoad() {},
	methods: {
		isFollow(e) {
			this.list[e].isFollow = true;
			uni.showToast({
				title: '关注成功'
			});
		},
		// 顶踩操作
		doSupport(e) {
			// 获取当前对象
			let item = this.list[e.index];
			let msg = e.type === 'support' ? '顶' : '踩';
			// 之前没有操作过
			if (item.support.type === '') {
				this.list[e.index].support.type = e.type;
				this.list[e.index].support[e.type + '_count']++;
				return;
			} else if (item.support.type === 'support' && e.type === 'unsupport') {
				// 顶-1
				item.support.support_count--;
				// 踩+1
				item.support.unsupport_count++;
			} else if (item.support.typpe === 'unsupport' && e.type === 'support') {
				//之前踩了
				// 顶+1
				item.support.support_count++;
				// 踩-1
				item.support.unsupport_count--;
			}
			item.support.type = e.type;
			      uni.showToast({
			        title: msg + "成功",
			      });
		}
	}
};
</script>

